.login{ background-image: linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)),url('../../public/image/HeaderBg.jpeg');
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}
.login-popup{
  position: relative;
  background-color: rgba(0,0,0,0.8);
  border-radius: 20px;
  width: 500px; 
  height: 90%;  
  padding: 50px;
  *{
    margin: 15px 0;
    font-size: $font-size-m;
  }
  label,input,span,button{
    display: block;
    width: 100%;
    border-radius: 5px;
    outline: none;
  }
  .close{
    background-color: transparent;
    position: absolute;
    right: 50px;
    top: 10px;
    font-size: 30px;
    cursor: pointer;
    color: #888;
    &:hover{
      color: #fff;
    }
  }
  h2{
    font-size: $font-size-l;
  }
  a{
    text-decoration: none;
    color: inherit;
    &:hover{
      text-decoration: underline;
    }
  }
  input{
    border: 1px solid #fff;
    padding: 10px;
    background-color: transparent;
    &:focus{
      // box-shadow: -2px -2px 0 #fff, //왼쪽 위
      // 2px -2px 0 #fff, //오른쪽 위
      // -2px 2px 0 #fff, //왼쪽 아래
      // 2px 2px 0 #fff; //오른쪽 아래
      box-shadow: 0 0 2px #fff
    }
  }
  button:nth-of-type(1){
    @include button-style;
  }
  button:nth-of-type(2){
    @include button-style;
    background-color: #444;
  }
  form{
    label{
      position: relative;
      &.focused{
        span{
          font-size: 10px;
          top: 20%;
          left: 10px;
        }
      }
      &.error{
        input{
          border: 2px solid red;
          &:focus{
            border: 1px solid #fff;
          }
        }
      }
    }
    input{
      position: relative;
      padding-top: 25px;
    }
    span{
      margin: 0;
      position: absolute;
      top: 50%;
      left: 15px;
      transform: translateY(-50%);
      transition: 0.2s;
    }
    p{
      text-align: center;
    }
    a{
      display: block;
      text-align: center;
    }
    .id-error{
      color: $primary-color;
      font-weight: bolder;
      font-size: 12px;
      text-align: left;
    }
  }
  .login-box{
    input[type=checkbox]{
      display: inline-block;
      width: 40px;
      height: 18px;
      margin: 0;
      // transform: scale(1.5);
      cursor: pointer;
      &:focus{
        outline: none;
        box-shadow: none;
      }
    }
    span{
      display: inline-block;
      width: auto;
    }
    p:last-of-type{
      font-size: 12px;
      a{
        font-size: 13px;
        color: dodgerblue;
      }
    }
  }
}